.RegisterWeekTimeTableModal > .modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
}
.RegisterWeekTimeTableModal > .modal > section {
  width: 90%;
  max-width: 1300px;
  margin:0 auto;
  border-radius: .3rem;
  background-color: #fff;
  animation: modal-show .3s;
  overflow: hidden;
}
.RegisterWeekTimeTableModal > .modal > section > header > .RegisterWeekTimeTableModal_header {
  position: relative;
  padding: 16px 64px 16px 16px;
  background-color: #f1f1f1;
  font-weight: 700;
  font-size: large;
}
.RegisterWeekTimeTableModal > .modal > section > main > .Main_content {
  padding: 16px;
  border-bottom: 1px solid #dee2e6;
  border-top: 1px solid #dee2e6;
}
.RegisterWeekTimeTableModal > .modal > section > footer > .RegisterWeekTimeTableModal_footer {
  padding: 12px 16px;
  text-align: right;
}
.RegisterWeekTimeTableModal > .modal > main >.Main_content {
  display: flex;
  flex-direction: row;
}
.RegisterWeekTimeTableModal > .modal.openModal {
  display: flex;
  align-items: center;
  animation: modal-bg-show .3s;
}
@keyframes modal-show {
  from {
      opacity: 0;
      margin-top: -50px;
  }
  to {
      opacity: 1;
      margin-top: 0;
  }
}
@keyframes modal-bg-show {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}

.Main_content{
  display: flex;
  flex-direction: row;
}