.AddressModal_header {
  display: flex;
  font-size: larger;
  justify-content: space-between;
  padding: 1rem;
  font-weight: bold;
  background-color: #f1f1f1;
}

.AddressModal > .modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
}
.AddressModal > .modal > section {
  width: 100%;
  max-width: 600px; 
  height: 100%;
  max-height: 500px;
  margin:0 auto;
  border-radius: .3rem;
  background-color: #fff;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-show .3s;
  overflow: hidden;
}

.AddressModal > .modal > section > main {
  padding: 16px;
  border-bottom: 1px solid #dee2e6;
  border-top: 1px solid #dee2e6;
}
.AddressModal > .modal.openModal {
  display: flex;
  align-items: center;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-bg-show .3s;
}
@keyframes modal-show {
  from {
      opacity: 0;
      /* margin-top: -50px; */
  }
  to {
      opacity: 1;
      margin-top: 0;
  }
}
@keyframes modal-bg-show {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}